import axios, {Axios} from "axios";
import { UserEndpoint } from "./endpoints/user.endpoint";

export class ApiClient {
    private client: Axios;
    public user: UserEndpoint;

    private constructor(
        base: string,
        token: string,
    ) {
        this.client = axios.create({
            baseURL: base,
            headers: {
                authorization: token
            }
        });
        this.user = new UserEndpoint(this.client);
    }

    public static async create(base: string, token: string): Promise<ApiClient> {
        return new ApiClient(base, token);
    }
}